export var COLUMN_SWITCH_TYPE_EVENT = 'switch_type';
export var initTypeSelector = function (column) {
    var select = column.getElement().querySelector('select.ac-setting-input_type');
    if (!select) {
        return;
    }
    select.addEventListener('change', function () {
        column.events.emit(COLUMN_SWITCH_TYPE_EVENT, { column: column, type: select.value });
        //AdminColumns.events.emit(EventConstants.SETTINGS.COLUMN.SWITCH, { column: column, type: select.value });
    });
};
